<template>
  <div id="simple">
    <div class="search">
      <label>最高</label>
      <input type="number" v-model="max" />
      <label>最低</label>
      <input type="number" v-model="min" />
      <button class="btn" @click="filter()">過濾</button>
      <button class="btn" @click="raw()">原始</button>
      <button class="btn" @click="showLike()">最愛</button>
    </div>
    <div class="container" @scroll="infinity($event)">
      <div
        class="item"
        v-for="(item, i) in data"
        v-show="item.totalPrice <= max && item.totalPrice >= min"
        :key="i"
      >
        <div class="carousel">
          <button class="next" @click="next(item)"></button>
          <div
            v-for="(img, imgIndex) in item.phots"
            v-show="imgIndex === item.imgStatus"
            :key="imgIndex"
          >
            <img :src="img" />
          </div>
          <button class="prev" @click="prev(item)"></button>
        </div>
        <div class="content">
          <span>{{ i }}</span>
          <div class="total">總共:{{ item.totalPrice }}</div>
          <div class="price">月租金:{{ item.price }}</div>
          <div class="photoNum">管理費:{{ item.HOA }}</div>
          <div class="link">
            <a
              target="_blank"
              :href="'https://rent.591.com.tw/rent-detail-' + item.id + '.html'"
              >點我看屋</a
            >
          </div>
          <div>
            <button class="funBtn" @click="hide(item, i)">隱藏</button>
            <button class="funBtn" @click="like(item, i)">最愛</button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // 模擬資料
      rawData: [],
      data: [],
      max: 6800,
      min: 4000,
      rawDataCount: 0,
    };
  },
  mounted() {
    fetch("/data.json")
      .then((res) => res.json())
      .then((data) => {
        const vm = this;
        vm.rawData = data.map((e) => {
          e.imgStatus = 0;
          console.log(e.coord);
          return e;
        });
        vm.data = vm.rawData.filter((e, i) => (i < 100) && vm.DF(e.coord));
        vm.rawDataCount = vm.data.length;
        this.filter();
      });
  },
  methods: {
    DF(coord) {
			const x = 24.17062182176869
			const y = 120.63770620779998
			const r = coord[0]
			const s = coord[1]
      return Math.sqrt(Math.pow(x - r, 2) + Math.pow(y - s, 2)) < 0.02879344174804847
    },
    next(i) {
      i.imgStatus++;
      if (i.imgStatus + 1 > i.imgs.length) {
        i.imgStatus = 0;
      }
    },

    prev(i) {
      i.imgStatus--;
      if (i.imgStatus < 0) {
        i.imgStatus = i.imgs.length - 1;
      }
    },
    hide(item, index) {
      const vm = this;
      let hideData = JSON.parse(localStorage.getItem("hide"));
      if (!hideData) {
        hideData = [];
      }
      if (hideData.indexOf(item.id) < 0) {
        hideData.push(item.id);
      }
      vm.data = vm.data.filter((e, i) => i !== index);
      localStorage.setItem("hide", JSON.stringify(hideData));
    },
    like(item) {
      let likeData = JSON.parse(localStorage.getItem("like"));
      if (!likeData) {
        likeData = [];
      }
      if (likeData.indexOf(item.id) < 0) {
        likeData.push(item.id);
      }
      localStorage.setItem("like", JSON.stringify(likeData));
    },
    filter() {
      const vm = this;
      let hideData = JSON.parse(localStorage.getItem("hide"));
      if (hideData) {
        vm.data = vm.data.filter((e) => {
          return hideData.indexOf(e.id) < 0;
        });
      }
    },
    raw() {
      this.data = this.rawData.filter((e, i) => i < 100);
    },
    showLike() {
      const vm = this;
      let likeData = JSON.parse(localStorage.getItem("like"));
      if (likeData) {
        vm.data = vm.rawData.filter((e) => {
          return likeData.indexOf(e.id) >= 0;
        });
      }
    },
    infinity(event) {
      const vm = this;
      const currHeight = event.target.offsetHeight + event.target.scrollTop;
      const totalHeight = event.target.scrollHeight - 500;
      if (currHeight > totalHeight) {
        vm.rawDataCount += 100;
        vm.data = vm.rawData.filter((e, i) => (i < vm.rawDataCount)&& vm.DF(e.coord));
      }
    },
  },
};
</script>
