import Vue from 'vue'
import VueRouter from 'vue-router'
import Map from '../views/Map.vue'
import Index from '../views/Index.vue'


Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Map',
    component: Map
  },
  {
    path: '/home',
    name: 'Index',
    component: Index
  }
]

const router = new VueRouter({
  routes
})

export default router
